  .profile-container .profile-blogs .main-heading {
    margin-top: 2rem;
  }

  .profile-container .profile-card {
    width: 90%;
    margin-top: 3rem;
  }

  .profile-container .card .card-body .basic-profile {
    justify-content: flex-start;
  }

  .profile-container .profile-blogs {
    width: 90%;
  }

  .profile-container .profile-blogs .blog-card {
    width: 100%;
  }
}

.search-container {
  min-height: calc(100vh - 34px);
}

.search-container .main-heading {
  font-size: 2.5rem;
  font-weight: 700;
  margin-left: 4rem;
  margin-top: 1rem;
  color: #368dd6;
}

.search-container .loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.search-container .loader svg {
  width: 200px;
  height: 200px;
}

.search-container .container {
  padding-top: 3vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(235, 235, 235);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 6px;
  margin-bottom: 30px;
}

.search-container .blog-card {
  margin-bottom: 30px;
  width: 40rem;
  border-radius: 12px;
  cursor: pointer;
}

.search-container .blog-card .card-img-top {
  height: 30vh;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.search-container .blog-card .card-body {
  padding: 0.5rem;
}

.search-container .blog-card .card-body h1 {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.search-container .blog-card .card-body h1:hover {
  color: #368dd6;
}

.search-container .blog-card .card-body .blog-info {
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 0.1rem;
  color: #181717;
}

.search-container .blog-card .card-body .blog-items {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1rem;
  font-weight: 600;
  color: #181717;
  margin-top: 1rem;
}

.search-container .blog-card .card-body .blog-items span {
  padding-right: 1.7rem;
}

@media (max-width: 992px) {
  .search-container {
    flex-direction: column;
  }

  .search-container .blog-card {
    margin-bottom: 30px;
    width: 20rem;
    border-radius: 12px;
    cursor: pointer;
  }
}

.signup-container{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 34px);
}

.signup-container .card{
    width: 45rem;
    padding: 1rem 0.5rem;
    border: 1px solid #434244;
    border-radius: 10px;
}

.signup-container .card .card-title{
    font-size: 2.5rem;
    font-weight: bold;
    color: #343335;
    text-align: center;
}

.signup-container .card form input{
    width: 90%;
}

.signup-container .card form button{
    background-color: #c67cf1;
    width: 10vw;
    color: #ffffff;
    border-radius: 10px;
    font-size: 1.3rem;
    font-weight: 500;
    cursor: pointer;
    border: #c67cf1;
}

.signup-container .card .error p{
    color: #c93c23;
    font-size: 1.4rem;
    text-align: center;
    font-weight: bold;
    padding-top: 2vh;
}
